import { auth, functions } from "@/firebaseInit";
import router from "@/router";
import firebaseAuthService from "@/services/FirebaseAuthService";
import { FirestoreService } from "@/services/FirestoreService";
import store from "@/store";
import { httpsCallable } from "firebase/functions";
import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class MobileVerify extends Vue {
  @Prop({ default: "" })
  mobileNumber!: string;
  countDown = 30;
  otpText = "Send OTP";
  otpShow = false;
  otpLoading = false;
  mobileInputDisabled = false;
  showAlert = false;
  alertText = "";
  sendOTPloading = false;
  sendOTPBtnDisabled = false;
  alertColor = "green";
  inputOTP = 0;
  resendCountDown = "";
  async sendOTP() {
    if (
      (this.$refs.mobileForm as Vue & { validate: () => boolean }).validate()
    ) {
      try {
        this.sendOTPloading = true;
        this.mobileInputDisabled = true;
        const user = auth.currentUser;
        const sendOTP = httpsCallable(functions, "sendOTP");
        const resp = await sendOTP({
          uid: user?.uid,
          mobileNumber: this.mobileNumber
        });
        this.sendOTPBtnDisabled = true;
        this.sendOTPloading = false;
        this.setAlert(
          "green",
          `Please enter the OTP sent on ${this.mobileNumber}`
        );
        this.countDownTimer();
        this.handleClearInput();
      } catch (error) {
        console.log(error);
        this.sendOTPBtnDisabled = false;
        this.mobileInputDisabled = false;
      }
    }
  }
  private setAlert(color: string, msg: string) {
    this.alertText = msg;
    this.showAlert = true;
    this.alertColor = color;
  }

  countDownTimer() {
    if (this.countDown > 0) {
      setTimeout(() => {
        this.countDown -= 1;
        this.otpText = `Resend OTP?`;
        this.resendCountDown = `Please wait ${this.countDown} seconds`;

        this.countDownTimer();
      }, 1000);
    } else {
      this.otpText = `Send OTP`;
      this.resendCountDown = "";
      this.sendOTPBtnDisabled = false;
    }
  }


  handleClearInput() {
    (this.$refs.otpInput as Vue & { clearInput: () => boolean }).clearInput();
  }
  clearMobileInput() {
    this.mobileInputDisabled = false;
    this.handleClearInput();
  }
  async verifyOTP(otp: number) {
    try {
      console.log(otp);
      this.otpLoading = true;
      this.sendOTPBtnDisabled = true;
      const user = auth.currentUser;
      if (user) {
        const verifyOTP = httpsCallable<OTPRequest, OTPResponse>(functions, "verifyOTP");
        const resp = await verifyOTP({ uid: user?.uid, otp: otp });
        if (resp.data.res === "ok") {
          this.showAlert = false;
          await FirestoreService.updateDocument("users", user.uid, {
            mobileNumber: this.mobileNumber,
            mobileVerified: true
          });
          await firebaseAuthService.addAuthParams(user);

          router.push("/");
        } else if (resp.data.res === "invalid_otp") {
          this.setAlert("red", `OTP is invalid. Please enter valid OTP`);
        }
      } else {
        store.commit("setSnack", "user not logged in");
      }
    } catch (error) {
      this.otpLoading = false;
      this.sendOTPBtnDisabled = false;
    } finally {
      this.otpLoading = false;
    }
  }
}
interface OTPRequest {
  uid: string;
  otp: number;
}
interface OTPResponse {
  res: string;
}